import React from "react";

import "../App.css";
import "./logo-musica.png"

const Navbar = () => {
  
  return (
    <div>
      <header className="mihead">
        <div className="miheadcon">
          <div className="miheadlink">
            <a>
              <img
                src="./logo-musica.png"
                alt="asd"
                width="100px"
                color="black"
                className="imagenicon"
              />
            </a>
            <a
           
              className="colorhead"
              href="https://instagram.com/bendito.diablo"
            >
              BENDITO DIABLO
            </a>
          </div>
        </div>
      </header>
  
    </div>
  );
};
export default Navbar;
