import React from "react";
import { Link } from "react-router-dom";
import "../App.css";

export default function Header(props) {
  return (
    <header className="headerglobal">
      <div className="jeadermain">
        <div className="maxheader">
          <header className="headerfinal">
            <div className="headerfinaldiv">
              <h1 className="headerfinaltitle">{props.title}</h1>
            </div>
            <div className="headerbutons">
              <div className="headerbutons , headerbutons2">
                <div className="botones-header">
                  <button className="bothead" >
                  <Link to={"/"}>🏠</Link>
                  </button>
                  <button className="bothead">
                    <Link to={"/contacts"}>📀</Link>
                  </button>
                  <button className="bothead">
                  <Link to={"/beats"}>🎙</Link></button>
                  
                </div>
              </div>
            </div>
          </header>
        </div>
      </div>
    </header>
  );
}
