import React from 'react'
import { Link } from "react-router-dom";

export default function Navigacion() {
  return (
    <nav className="navbar navbar-expand-lg bg-dark minavin ">
    <div className="container-fluid , micontenedor">
      <div
        className="collapse navbar-collapse , minavina"
        id="navbarNavDropdown"
      >
        <ul className="navbar-nav mx-auto , navmia  ">
          <li className="nav-item , navmain ">
            <Link
              className="nav-link active , navmain , botonnav"
              aria-current="page"
              to={"/"}
            >
              Inicio
            </Link>
          </li>
          <li className="nav-item , navmain ">
            <Link
              
              to={"/contacts"}
              className="nav-link active , navmain , botonnav"
            >
              Producción
            </Link>
          </li>
          <li className="nav-item , navmain">
          <Link
              className="nav-link active , navmain , botonnav"
              aria-current="page"
              to={"/beats"}
            >
              Beats
            </Link>
          </li>
          <li className="nav-item , navmain">
            <a
              className="nav-link active , navmain , botonnav"
              href="https://www.youtube.com/@benditodiablo"
              without
              rel="noreferrer"
              target="_BLANK"
            >
              Subir
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
  )
}
