import React from "react";
import ReactDOM from "react-dom/client";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import "./index.css";


import App from "./App";
import "./App.css";
import Navbar from "./Components/nav";

import ProduccionM from "./Components/ProduccionM";
import Navigacion from "./Components/Navigacion";
import Beats from "./Components/Beats";
import ErrorPage from "./error-page";

const router = createBrowserRouter([
  {
    path: "/benditodiablo",
    element: <App></App>,
    errorElement: <App></App>,
    
  },
  {
    path: "/benditodiablo/contacts",
    element: (
      <>
        <Navbar></Navbar>
        <Navigacion></Navigacion>
        <ProduccionM></ProduccionM>
      </>
    ),
  },
  {
    path: "/benditodiablo/beats",
    element: (
      <>
        <Navbar></Navbar>
        <Navigacion></Navigacion>
        <Beats></Beats>
      </>
    ),
  },
]);

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <>
      <RouterProvider router={router} />
    </>
  </React.StrictMode>
);

